import { PegawaiDetailService } from './../../../service/pegawai-detail.service';
import { Component, OnInit } from '@angular/core';
import { PayrollPegawaiKomponenService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Pegawai_komponen } from 'src/app/sdk/payroll/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private payrollPegawaiKomponenService: PayrollPegawaiKomponenService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService
    ) { }

    id_pegawai: string = ''
    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(
            data => {
                this.id_pegawai = data;
                this.getList()
            }
        )

    }


    listKomponen: any[] = []
    getList() {
        this.spinner.show()
        this.payrollPegawaiKomponenService.getPayrollPegawaiKomponens(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.listKomponen = data.data
                this.spinner.hide()
            }
        )
    }

    gantiModeEnable(status, id_komponen_perusahaan) {
        console.log(status)
        this.spinner.show()
        this.payrollPegawaiKomponenService.postPayrollPegawaiKomponens({
            is_enable: status,
            id_pegawai: this.id_pegawai,
            id_komponen_perusahaan: id_komponen_perusahaan
        }).subscribe(
            data => {
                this.getList();
                this.spinner.hide()
            }
        )
    }

    gantiModeDefault(is_default, id_pegawai_komponen) {
        console.log(is_default)
        this.spinner.show()
        this.payrollPegawaiKomponenService.postPayrollPegawaiKomponens({
            is_default: is_default,
            id_pegawai: this.id_pegawai,
            id_pegawai_komponen: id_pegawai_komponen
        }).subscribe(
            data => {
                this.getList();
                this.spinner.hide()
            }
        )
    }

    pegawai_komponen: Pegawai_komponen = {}
    updatePegawaiKomponen(index, id_pegawai_komponen) {

        this.listKomponen.forEach((element, i) => {
            this.listKomponen[i].on_editable = false;
        });

        this.spinner.show();
        this.payrollPegawaiKomponenService.getPayrollPegawaiKomponensId(id_pegawai_komponen).subscribe(
            data => {
                this.pegawai_komponen = data.data;
                this.listKomponen[index].on_editable = true;
                this.spinner.hide();
            }
        )
    }

    simpanPegawaiKomponen(id_pegawai_komponen, param) {
        if (param.rumus == '' || param.rumus == null) {
            this.pesan.pesanWarningForm('rumus tidak boleh kosong');
            return false;
        }

        this.spinner.show();
        this.payrollPegawaiKomponenService.putPayrollPegawaiKomponensId({ id: id_pegawai_komponen, body: param }).subscribe(
            data => {
                this.getList();
                this.spinner.hide()
                this.pesan.pesanBerhasilForm('Berhasil di perbarui');
            }
        )
    }

    //DRAWER
    visible = false;

    open(): void {
        this.visible = true;
    }

    close(): void {
        this.visible = false;
    }

}
