import { Component, OnInit } from '@angular/core';
import { KalenderLemburPengaturanService, KalenderCutiPengaturanService } from 'src/app/sdk/hr/services';
import { Lembur_pengaturan, Cuti_pengaturan } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private kalenderLemburPengaturanService: KalenderLemburPengaturanService,
        private kalenderCutiPengaturanService: KalenderCutiPengaturanService,
    ) { }


    ngOnInit() {
        this.getParamaterLembur()
        this.getParamaterCuti()
    }


    list_lembuer_param: Lembur_pengaturan[] = []
    getParamaterLembur() {
        this.kalenderLemburPengaturanService.getKalenderLemburPengaturans().subscribe(
            data => {
                this.list_lembuer_param = data.data
            }
        )
    }

    list_cuti_param: Cuti_pengaturan[] = []
    getParamaterCuti() {
        this.kalenderCutiPengaturanService.getKalenderCutiPengaturans().subscribe(
            data => {
                this.list_cuti_param = data.data
            }
        )
    }

}
