import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { KomponenRoutingModule } from './komponen-routing.module';
import { ListComponent } from './list/list.component';
import { EditComponent } from './edit/edit.component';
import { NzSelectModule, NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';


@NgModule({
  declarations: [ListComponent, EditComponent],
  imports: [
    CommonModule,
    KomponenRoutingModule,
    NgZorroAntdModule,
    NzSelectModule,
    NgZorroAntdModule,
    ReactiveFormsModule,
    FormsModule
],
providers: [
    FormBuilderTypeSafe
]
})
export class KomponenModule { }
